import React from "react";
    import * as SliderPrimitive from "@radix-ui/react-slider";
    import { cn } from "@/lib/utils";

    const Slider = React.forwardRef(({ className, ...props }, ref) => (
      <SliderPrimitive.Root
        ref={ref}
        className={cn(
          "relative flex w-full touch-none select-none items-center group",
          className
        )}
        {...props}>
        <SliderPrimitive.Track className="relative h-2 w-full grow overflow-hidden rounded-full bg-secondary/20">
          <SliderPrimitive.Range className="absolute h-full bg-primary group-hover:bg-primary-hover transition-colors" />
        </SliderPrimitive.Track>
        {/* Handle multiple thumbs if needed, props.value should be an array */}
        {(Array.isArray(props.value) ? props.value : [props.value]).map((_, i) => (
          <SliderPrimitive.Thumb
            key={i}
            className="block h-5 w-5 rounded-full border-2 border-primary bg-background ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 group-hover:border-primary-hover" />
        ))}
      </SliderPrimitive.Root>
    ));
    Slider.displayName = SliderPrimitive.Root.displayName;

    export { Slider };